import React from 'react';
import { IconButton, Popover, List, ListItem, ListItemIcon, ListItemText, ListItemSecondaryAction, Switch } from '@material-ui/core';
import { Settings, Visibility, SkipNext } from '@material-ui/icons';

export default class SettingsButton extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			settingsOpen: false,
		};
		this.anchor = React.createRef();
	}

	render() {
		return (
			<>
				<IconButton ref={this.anchor} onClick={() => this.setState({ settingsOpen: !this.state.settingsOpen })}>
					<Settings />
				</IconButton>
				<Popover
					onClose={() => this.setState({ settingsOpen: false })}
					open={this.state.settingsOpen}
					anchorEl={this.anchor.current}
					anchorOrigin={{
						vertical: 'bottom',
						horizontal: 'center',
					}}
					transformOrigin={{
						vertical: 'top',
						horizontal: 'center',
					}}
				>
					<List style={{ padding: '1em' }}>
						<ListItem>
							<ListItemIcon>
								<Visibility />
							</ListItemIcon>
							<ListItemText primary="Show All Synopses" style={{ marginRight: '1em' }} />
							<ListItemSecondaryAction>
								<Switch
									edge="end"
									checked={this.props.settings.showSynopses}
									onChange={(e, active) => this.props.onChange({ showSynopses: active })}
								/>
							</ListItemSecondaryAction>
						</ListItem>
						<ListItem>
							<ListItemIcon>
								<SkipNext />
							</ListItemIcon>
							<ListItemText primary="Load Next Episodes Automatically" style={{ marginRight: '1em' }} />
							<ListItemSecondaryAction>
								<Switch
									edge="end"
									checked={this.props.settings.autoNext}
									onChange={(e, active) => this.props.onChange({ autoNext: active })}
								/>
							</ListItemSecondaryAction>
						</ListItem>
					</List>
				</Popover>
			</>
		);
	}
};