import React from 'react';
import { Player, BigPlayButton } from 'video-react';
import "../node_modules/video-react/dist/video-react.css";
import video from './video';

export default class SUPlayer extends React.Component {
	constructor(props) {
		super(props);
		let startTime = window.localStorage.getItem('startTime');
		if (startTime) {
			startTime = parseFloat(startTime);
		} else {
			startTime = 0;
		}
		
		let base = window.localStorage.getItem('videobase');
		if (!base) {
			base = video.base;
		}
		this.state = {
			hasPlayed: false,
			video: props.video,
			startTime: startTime,
			base: base
		};
		this.player = React.createRef();
	}
	
	componentDidMount() {
	}

	UNSAFE_componentWillReceiveProps(props) {
		if (props.video.name !== this.props.video.name) {
			this.setState({
				hasPlayed: false,
				video: props.video,
				startTime: 0
			});
			window.localStorage.removeItem('startTime');
			this.player.current.load();
		}
	}

	render() {
		return (
			<Player
				ref={this.player}
				startTime={this.state.video.thumbnail ? this.state.video.thumbnail : 0}
				preload="auto"
				muted={true}
				onPlay={() => {
					if (!this.state.hasPlayed) {
						this.player.current.muted = true;
					}
				}}
				onTimeUpdate={() => {
					const { player } = this.player.current.getState();
					if (this.state.hasPlayed)
						window.localStorage.setItem('startTime', player.currentTime);
					else if (!player.paused && !player.waiting) {
						this.player.current.muted = false;
						this.player.current.seek(this.state.startTime);
						this.setState({ hasPlayed: true });
					}
				}}
				onEnded={this.props.onNext}
			>
				<BigPlayButton position="center" />
				<source src={this.state.video.url} />
			</Player>
		);
	}
};