import React from 'react';
import { List, ListItem, ListItemIcon, ListItemText, Collapse, Typography } from '@material-ui/core'
import video from './video';
import { ExpandLess, ExpandMore } from '@material-ui/icons';

export default class EpisodeList extends React.Component {
	constructor(props) {
		super(props);
		let a = new Array(video.seasons.length).fill(false);
		a[props.selectedSeason] = true;
		this.state = {
			hidden: true,
			open: a
		};
		this.list = React.createRef();
		this.selected = React.createRef();
	}

	handleClick(i) {
		let newOpen = this.state.open.slice();
		newOpen[i] = !newOpen[i];
		this.setState({
			open: newOpen
		});
	}
	updateDimensions = () => {
		this.list.current.style.height = (window.innerHeight - this.list.current.getBoundingClientRect().top - 32) + 'px';
	}
	componentDidMount() {
		this.updateDimensions();
		this.list.current.scrollTop = this.selected.current.offsetTop;
		window.addEventListener('resize', this.updateDimensions);
	}
	componentWillUnmount() {
		window.removeEventListener('resize', this.updateDimensions);
	}

	render() {
		return (
			<List
				component="nav"
				style={{
					overflow: 'auto'
				}}
				ref={this.list}
			>
				{
					video.seasons.map((season, i) => {
						let selected;
						if (season.standalone) {
							// eslint-disable-next-line
							selected = this.props.selectedSeason == i;
							return <ListItem key={i} button style={{ color: "white" }} selected={selected} ref={selected ? this.selected : null} onClick={() => this.props.onSelect(i, "standalone")}>
								<ListItemText primary={season.name} />
							</ListItem>;
						} else {
							return (
								<div key={i}>
									<ListItem button onClick={() => this.handleClick(i)} style={{ color: "white" }} >
										<ListItemText primary={season.name} />
										{this.state.open[i] ? <ExpandLess /> : <ExpandMore />}
									</ListItem>
									<Collapse in={this.state.open[i]} >
										<List component="div" disablePadding>
											{
												season.episodes.map((episode, ei) => {
													// eslint-disable-next-line
													selected = this.props.selectedSeason == i && this.props.selectedEpisode == ei;
													return (
														<ListItem key={ei} button style={{ color: "white" }} selected={selected} ref={selected ? this.selected : null} onClick={() => this.props.onSelect(i, ei)}>
															<ListItemIcon>
																<Typography><b>{episode.id.split('e')[1]}</b></Typography>
															</ListItemIcon>
															<ListItemText primary={episode.name} />
														</ListItem>
													);
												})
											}
										</List>
									</Collapse>
								</div>
							);
						}
					})
				}
			</List>
		);
	}
};