import React from 'react';
import './App.css';
import { AppBar, Toolbar, Typography, Grid, Box, Hidden, Card, Drawer, IconButton } from '@material-ui/core';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import Player from './Player';
import Synopsis from './Synopsis';
import EpisodeList from './EpisodeList';
import SettingsButton from './SettingsButton';
import video from './video';
import { Menu } from '@material-ui/icons';

class App extends React.Component {
  constructor(props) {
    super(props);
    if (window.localStorage.getItem('season') === null)
      this.resetCurrentEpisode();
    if (window.localStorage.getItem('settings') === null)
      window.localStorage.setItem('settings', JSON.stringify({ showSynopses: false, autoNext: true }));
    this.state = {
      season: window.localStorage.getItem('season'),
      episode: window.localStorage.getItem('episode'),
      settings: JSON.parse(window.localStorage.getItem('settings')),
      drawerOpen: false
    };
  }

  setSettings(newSettings) {
    let settings = Object.assign(this.state.settings, newSettings);
    this.setState({
      settings: settings
    });
    window.localStorage.setItem('settings', JSON.stringify(settings));
  }

  resetCurrentEpisode() {
    window.localStorage.setItem('season', 1);
    window.localStorage.setItem('episode', 0);
  }

  getCurrentEpisode() {
    let season = video.seasons[this.state.season];
    if (this.state.episode === 'standalone') {
      if (!season || !season.standalone) {
        this.resetCurrentEpisode();
        return video.seasons[1].episodes[0];
      }
      return video.seasons[this.state.season];
    } else {
      if (!season || !season.episodes[this.state.episode]) {
        this.resetCurrentEpisode();
        return video.seasons[1].episodes[0];
      }
      return video.seasons[this.state.season].episodes[this.state.episode];
    }
  }

  nextEpisode() {
    if (!this.state.settings.autoNext) return;
    if (this.state.episode === 'standalone') {
      return;
      // eslint-disable-next-line
    } else if (video.seasons[this.state.season].episodes.length - 1 == this.state.episode) {
      return;
    } else {
      this.setCurrentEpisode(this.state.season, parseInt(this.state.episode) + 1)
    }
  }

  setCurrentEpisode(season, episode) {
    window.localStorage.setItem('season', season);
    window.localStorage.setItem('episode', episode);
    this.setState({
      season: season,
      episode: episode,
      drawerOpen: false
    });
  }

  render() {
    var season = video.seasons[this.state.season]
    var episode = this.getCurrentEpisode();
    const theme = createMuiTheme({
      palette: {
        type: 'dark',
        primary: season.shirt,
        secondary: {
          light: '#ffff60',
          main: '#ffef1e',
          dark: '#c8bd00',
          contrastText: '#000',
        },
      },
    });
    var card =
      <Card>
        <Box m={4}>
          <Typography variant="h4" style={{ margin: "0 auto" }}>
            {episode.name}
          </Typography>
          {
            season.standalone ? null :
              <Typography variant="h5" style={{ margin: "0 auto" }}>
                {season.name} Episode {episode.id.split('e')[1]}
              </Typography>
          }
          <div style={{ marginTop: "2em" }} />
          <Synopsis show={this.state.settings.showSynopses}>
            {episode.synopsis}
          </Synopsis>
          <Typography className="spoiler" style={{ marginTop: "2em" }}>
          </Typography>
        </Box>
      </Card>;
    var episodeList =
      <EpisodeList selectedSeason={this.state.season} selectedEpisode={this.state.episode} onSelect={(s, e) => this.setCurrentEpisode(s, e)}>
      </EpisodeList>;
    return (
      <ThemeProvider theme={theme}>
        <div className="App">
          <AppBar position="static" color="primary">
            <Toolbar>
              <Hidden xsDown>
                <svg className="App-logo" width="178" height="178" viewBox="111,41,179,179">
                  <path stroke={season.shirt.starStroke ? season.shirt.starStroke : 'none'} strokeWidth={8} fill={season.shirt.star} fillRule="evenodd" d="M199.869 48.749 C 196.569 49.566,186.783 68.862,176.459 94.909 C 173.761 101.717,173.581 101.829,164.360 102.478 C 135.357 104.520,117.925 107.154,115.301 109.893 C 111.971 113.369,119.417 121.541,147.128 144.822 C 157.775 153.767,157.161 152.031,154.068 164.456 C 147.651 190.229,144.843 206.810,146.311 210.256 C 147.358 212.713,149.556 213.052,153.747 211.404 C 160.667 208.683,177.377 199.284,193.734 188.913 C 200.971 184.324,199.808 184.174,211.227 191.172 C 239.061 208.232,250.968 213.950,253.761 211.600 C 256.472 209.319,254.614 196.424,247.011 164.752 C 243.974 152.100,243.657 153.314,251.701 146.746 C 270.036 131.775,283.949 118.538,286.170 113.952 C 287.537 111.128,286.208 108.969,282.353 107.752 C 276.275 105.833,261.038 103.875,243.342 102.741 C 239.824 102.515,235.331 102.227,233.359 102.101 C 227.563 101.729,227.412 101.583,223.899 92.950 C 213.684 67.846,205.913 51.852,202.791 49.508 C 201.905 48.842,200.727 48.537,199.869 48.749" />
                </svg>
                <Typography variant="h5" style={{ margin: "0 auto" }}>
                  {episode.name}
                </Typography>
                <div style={{ position: 'absolute', right: 0 }}>
                  <SettingsButton settings={this.state.settings} onChange={(newSettings) => this.setSettings(newSettings)} />
                </div>
              </Hidden>
              <Hidden smUp>
                <IconButton style={{ position: 'absolute', left: 0 }} onClick={() => this.setState({drawerOpen: true})}>
                  <Menu />
                </IconButton>
                <svg className="App-logo" width="178" height="178" viewBox="111,41,179,179" style={{ margin: "0 auto", position: 'inherit' }}>
                  <path stroke={season.shirt.starStroke ? season.shirt.starStroke : 'none'} strokeWidth={8} fill={season.shirt.star} fillRule="evenodd" d="M199.869 48.749 C 196.569 49.566,186.783 68.862,176.459 94.909 C 173.761 101.717,173.581 101.829,164.360 102.478 C 135.357 104.520,117.925 107.154,115.301 109.893 C 111.971 113.369,119.417 121.541,147.128 144.822 C 157.775 153.767,157.161 152.031,154.068 164.456 C 147.651 190.229,144.843 206.810,146.311 210.256 C 147.358 212.713,149.556 213.052,153.747 211.404 C 160.667 208.683,177.377 199.284,193.734 188.913 C 200.971 184.324,199.808 184.174,211.227 191.172 C 239.061 208.232,250.968 213.950,253.761 211.600 C 256.472 209.319,254.614 196.424,247.011 164.752 C 243.974 152.100,243.657 153.314,251.701 146.746 C 270.036 131.775,283.949 118.538,286.170 113.952 C 287.537 111.128,286.208 108.969,282.353 107.752 C 276.275 105.833,261.038 103.875,243.342 102.741 C 239.824 102.515,235.331 102.227,233.359 102.101 C 227.563 101.729,227.412 101.583,223.899 92.950 C 213.684 67.846,205.913 51.852,202.791 49.508 C 201.905 48.842,200.727 48.537,199.869 48.749" />
                </svg>
                <div style={{ position: 'absolute', right: 0 }}>
                  <SettingsButton settings={this.state.settings} onChange={(newSettings) => this.setSettings(newSettings)} />
                </div>
              </Hidden>
            </Toolbar>
          </AppBar>
          <Hidden smUp>
            <Drawer
              variant="temporary"
              anchor="left"
              open={this.state.drawerOpen}
              onClose={() => this.setState({drawerOpen: false})}
            >
              {episodeList}
            </Drawer>
          </Hidden>
          <Grid container spacing={3} style={{ padding: "1em" }}>
            <Hidden mdDown>
              <Grid item xs={12} lg={3}>
                {card}
              </Grid>
            </Hidden>
            <Grid item xs={12} sm={8} lg={6}>
              <Player video={episode} onNext={() => this.nextEpisode()} />
              <Hidden lgUp>
                {card}
              </Hidden>
            </Grid>
            <Hidden xsDown>
              <Grid item xs={12} sm={4} lg={3}>
                {episodeList}
              </Grid>
            </Hidden>
          </Grid>
        </div>
      </ThemeProvider>
    );
  }
}

export default App;
