import React from 'react';
import { Typography } from '@material-ui/core';

export default class Synopsis extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			hidden: !props.show,
		};
	}

	UNSAFE_componentWillReceiveProps(props) {
		if (this.props.children !== props.children)
			this.setState({
				hidden: !props.show
			});
		else if (this.state.hidden === props.show) {
			this.setState({
				hidden: !props.show
			});
		}
	}

	render() {
		return (
			<>
				{this.state.hidden ?
					<Typography>
						Synopsis (click to show)
					</Typography>
					: null
				}
				<Typography className={"spoiler" + (this.state.hidden ? " hidden" : " shown")} onClick={() => this.setState({ hidden: false })}>
					{this.props.children}
				</Typography>
			</>
		);
	}
};